$blue-color: #08A6E4;
$background-color: #FFFFFF;
$nav-list-color: #A5A5A5;
$pink-color: #FB3B64;
$green-color: #55DFB4;
$font-raleway: "Raleway", sans-serif;
$font-opensans: "Open Sans", sans-serif;

* {
  margin: 0;
  padding: 0;
}

.header {
  display: flex;
  height: 70px;
  width: 100%;
  background: $background-color;
  box-shadow: 0 3px 6px #00000029;
  justify-content: center;
  position: fixed;
  z-index: 20;

  @media (min-width: 426px) {
    display: flex;
    height: 70px;
    width: 100%;
    background: $background-color;
    box-shadow: 0 3px 6px #00000029;
    justify-content: center;
    position: fixed;
    z-index: 20;
  }
}

.header-title, .footer-title {
  font-family: $font-raleway;
  font-weight: 300;
  font-size: 32px;
  color: $blue-color;
}

.container-header-footer {
  display: flex;
  height: 100%;
  width: 90%;
  /*background: rgba(0, 0, 0, 0.1);*/
  justify-content: space-between;
  align-items: center;
}

.nav {
  display: none;

  @media (min-width: 426px) {
    height: 70px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
  }
}

.nav-list {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 70px;
  box-sizing: border-box;
  font-family: "Open Sans", sans-serif;
  font-weight: 700;
  font-size: 11px;
  list-style: none;
  margin: 0 30px;
  text-transform: uppercase;
  color: #A5A5A5;
}

.nav-list:hover {
  color: #000000;
  border-bottom: 7px solid #08A6E4;
  cursor: pointer;
}

.main-top {
  width: 100vw;
  height: 100vh;
  background: transparent url("../images/Background.png") no-repeat;
  background-size: 200%;
  background-position: bottom;

  @media (min-width: 426px) {
    background-size: contain;
  }
}

.container-main-top {
  width: 100vw;
  display: flex;
  height: 50%;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;

  @media (min-width: 426px) {
    justify-content: flex-start;
  }
}

.main-top-h1, .main-top-h3, .main-top-btn {
  margin: 10px 10px;

  @media (min-width: 426px) {
    margin: 20px 20px;
  }
}

.main-top-h1 {
  font-family: $font-raleway;
  font-weight: 400;
  font-size: 35px;
  margin-top: 150px;

  @media (min-width: 426px) {
    font-size: 49px;
  }
}

.main-top-h3 {
  font-family: $font-opensans;
  font-weight: 400;
  font-size: 16px;
  text-align: center;
}

.btn {
  border-radius: 40px;
  outline: none;
}

.btn:hover {
  cursor: pointer;
}

.main-top-btn {
  box-sizing: border-box;
  font-family: $font-opensans;
  font-weight: 700;
  font-size: 16px;
  height: 65px;
  width: 85%;
  color: #FFFFFF;
  background: #08A6E4;
  margin-top: 30px;

  @media (min-width: 426px) {
    width: 440px;
  }
}

.stats {
  display: flex;
  height: 530px;
  background: #FFFFFF;
  justify-content: center;
}

.stats-title {
  text-align: center;
}

.container-stats-articles {
  @media (max-width: 425px) {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.single-stat-container {
  height: 140px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

.icon {
  height: 63px;
  width: 63px;
  border-radius: 50%;

  @media (max-width: 425px) {
    margin-top: 20px;
  }
}

.icon-1 {
  background: $pink-color;
  z-index: 0;

}

.icon-1::after {
  content: "";
  height: 63px;
  width: 63px;
  background: url("../images/Person.svg") no-repeat center;
  right: 40px;
  z-index: 1;
  position: absolute;

  @media (min-width: 426px) {
    right: 10px;
  }
}

.icon-2 {
  background: $blue-color;
}

.icon-2::after {
  content: "";
  height: 63px;
  width: 63px;
  background: url("../images/Eye.svg") no-repeat center;
  right: 45px;
  z-index: 1;
  position: absolute;
}

.icon-3 {
  background: $green-color;
}

.icon-3::after {
  content: "";
  height: 63px;
  width: 63px;
  background: url("../images/Catalog.svg") no-repeat center;
  right: 40px;
  z-index: 1;
  position: absolute;

  @media (min-width: 426px) {
    right: 20px;
  }
}

