.stats-title {
  font-family: $font-raleway;
  font-weight: 400;
  font-size: 30px;
  display: flex;
  height: 50%;
  width: 100%;
  justify-content: center;
  align-items: center;

  @media (min-width: 426px) {
    font-size: 32px;
  }
}

.stats-numbers {
  font-family: $font-raleway;
  font-weight: 800;
  font-size: 20px;
  letter-spacing: 6px;
}

.stats-description {
  font-family: $font-raleway;
  font-weight: 400;
  font-size: 17px;
  color: #A5A5A5;
}

.stats-icons {

  @media (min-width: 426px) {
    height: 50%;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
  }
}
